import React, { Component } from "react";
const search_icon = require("../../assets/search_icon.svg");
var Tokenizer = require("react-typeahead").Tokenizer;

export class SearchField extends Component {
  render() {
    return (
      <div className="search" ref="searchContain">
        <form ref="searchForm">
          <Tokenizer
            ref="tokenizer"
            options={["John", "Paul", "George", "Ringo"]}
            maxVisible={4}
            customClasses={{
              typeahead: "search-and-token-contain",
              input: "search-input",
              results: "search-result-container",
              listItem: "result-item",
              token: "result-token",
              customAdd: "topcoat-addme"
            }}
            placeholder={"Search by Title, Category, or Tags"}
            onTokenAdd={function(token) {
              //   self.addSearchString(token);
            }}
            onTokenRemove={function(token) {
              //   self.removeSearchString(token);
            }}
          />
        </form>
        <span className="icon-search" />
      </div>
    );
  }
}



